

.ant-card .card-list-item {
    position: relative;
}
.card-list-item {
    width: 300px;
}

.card-list-item-tag-list {
    display: flex;
    list-style:none;
    padding: 0;
    margin:0;
}
.card-list-item-price {
    position: absolute;
    padding:0 5px;
    top:20px;
    right:0;
    color:white;
    background-color:#1E90FF;
}
.tag-list-item {
    margin-right: 5px;
    color:#69c0ff;
}
.span {
    margin-right: 5px;
}
.tag-list-item:last-child .tag-list-item-comma {
    display:none;
}